import React from 'react';
import {graphql, Link} from 'gatsby';
import styled from '@emotion/styled';
import {rhythm} from '../../utils/typography';

const Summary = styled.p``;

export default ({blogSummary}) => {
  return (
    <div>
      <h3
        style={{
          marginBottom: rhythm(0.25),
        }}
      >
        <Link to={blogSummary.fields.slug}>
          {blogSummary.frontmatter.title || blogSummary.fields.slug}
        </Link>
      </h3>
      <small>{blogSummary.frontmatter.date}</small>
      <Summary>
        {blogSummary.frontmatter.summary || blogSummary.excerpt}
      </Summary>
      <hr />
    </div>
  );
};

export const query = graphql`
  fragment BlogSummary on MarkdownRemark {
    excerpt(format: PLAIN)
    fields {
      slug
    }
    frontmatter {
      title
      date(formatString: "MMMM DD, YYYY")
      tags
      summary
    }
  }
`;
